@import url('https://fonts.googleapis.com/css?family=Kaushan+Script|Roboto&display=swap');

body, html {
  height: 100%;
  margin: 0;
  margin: 0;
  padding: 0;
  font-family: Roboto, serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a {
  text-decoration: none;
}
.home{
  padding-top: 5rem;
  height: 100vh;
  padding-left: 1rem;
  padding-right: 1rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.form {
  max-width: 30rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  background-color: #ffffff;
  padding: 1rem 1rem;
}
.form > div{
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form > div > div{
border-bottom: 1px solid #838383;
}
.form > div > span{
  margin-top: 0.5rem;
  text-align: right;
  font-size: 0.9rem;
}

.form > div > div > i{
  margin-right: 0.5rem;
  color: #838383;
}

.title{
  font-weight: bold;
  text-align: center;
  font-size: 2.5rem;
}
.form div:last-child{
  text-align: center;
  font-weight: bold;
  /* margin-bottom: 1rem; */
}
.form label{
  margin: 1rem 0;
  font-weight: bold;
  /* display: block; */
}
.form input {
  border: none;
  outline: none;
  width: 90%;
  font-family: Roboto, serif;
  padding: 0.5rem;
  font-size: 1rem;
}
.btn {
  font-family: Roboto, serif;
  padding: 0.7rem;
  border-radius: 0.5rem;
  border: none;
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
}
.btn.primary {
  background: rgb(39,35,112);
  background: linear-gradient(90deg, rgba(39,35,112,1) 0%, rgba(70,70,125,1) 52%, rgba(0,212,255,1) 100%);

}
.btn.primary:hover{
  color: #929292;
}

.btn.secundary{
  background: #9F9D9D;
}
.btn.secundary:hover{
  background: #827E7E;
}

.btn.danger{
  background: #F42F2F;
}
.btn.danger:hover{
  background: #E11A1A;
}
.select{
  text-decoration: none;
  border: none;
  width: 90%;
  box-shadow: none;
}
/* Modal */
.modal {
  margin-top: 5rem;
}

/* form modal */
.form-modal {
  max-width: 30rem;
  margin: 0 auto;
  background-color: #ffffff;
  padding: 0.5rem 0.5rem;
}
.form-modal > div{
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form-modal > div > div{
  position: relative;
}

.form-modal > div > div > i{
  position: absolute;
  top: 0.5rem;
  right: 3%;
  width: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
}
.form-modal label {
  font-family: Roboto, serif;
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 0.2rem;
}
.form-modal > div > input {
  border: none;
  outline: none;
  font-family: Roboto, serif;
  font-size: 1rem;
  padding: 0.6rem;
  border-radius: 0.2rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

}

/* sub-form */
.sub-form{
  /* justify-content: space-between; */
  display: flex;
  flex-direction: row;
  width: 90%;
  margin-top: 1rem;
}
.sub-form > div > i {
  cursor: pointer;
  margin-top: 0.4rem;
  margin-left: 1rem;
  font-size: 1.5rem;
}
.sub-form > div > input{
  padding: 0.4rem;
  border: none;
  border-radius: 0.2rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.sub-form .btn{
  width: 5rem;
}

/* alert */
.alert{
  border-radius: 0.2rem;
  padding: 0.4rem 1rem;
  font-size: 1rem;
}
.alert-info{
  background-color: #BCBFFF;
  color: #7D8300;
}
.alert-danger{
  color: red;
  background-color: #00D5D4;
}

/* Header */

/* Style the header with a grey background and some padding */
.header {
  overflow: hidden;
  background-color: #f1f1f1;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  /* padding: 12px; */
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the active/current link*/
.header a.active {
  background-color: dodgerblue;
  color: white;
}

/* Float the link section to the right */
.header-right {
  margin-top: 1.2rem;
  float: right;
}
.header-right a{
  padding: 0.4rem;
}

.logo{
  padding: 0;
  width: 6rem;
  height: 5rem;
}
/* spinner */
.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border-left-color: #09f;
  margin: 0 auto;
  animation: spin 1s ease infinite;
  vertical-align: middle;
  margin-top: 1%;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* floating-button */
.floating-button{
  bottom: 5%;
  right: 5%;
  position: fixed;
}
.floating-button > button{
  cursor: pointer;
  border: none;
  background: none;
}
.floating-button > button > i{
  font-size: 3.5rem;
  color: #9F9D9D;
}